<template>
  <div class="main">
    <!-- 标题 -->
    <div class="add_title">
      添加乡村
    </div>
    <div class="hengg1"></div>
    <!-- 各种信息填写 -->
    <form class="clear form">
      <p class="formtr formtrp">
        <span>名称：</span>
        <input type="text" class="text-input input-long" v-model="xcName">
      </p>
      <div class="formtr formtrDiv">
        <span>简介：</span>
        <textarea class="textarea" rows="13" v-model="xcIntroduction"></textarea>
      </div>
      <p class="formtr formtrp">
        <span>隶属：</span>
        <select name="" id="location1" class="select" style="width: 110px; margin-right:10px;">
          <option value="1">浙江省</option>
        </select>
        <select name="" id="location2" class="select" style="width: 110px; margin-right:10px;">
          <option value="1">湖州市</option>
        </select>
        <select name="" id="location3" class="select" style="width: 110px; margin-right:10px;">
          <option value="1">安吉县</option>
        </select>
        <select name="" id="location4" class="select" style="width: 110px; margin-right:10px;" v-model="lishuxian">
          <option value="递铺镇">递铺镇</option>
          <option value="杭垓镇">杭垓镇</option>
          <option value="孝丰镇">孝丰镇</option>
          <option value="皈山乡">皈山乡</option>
          <option value="良朋镇">良朋镇</option>
          <option value="鄣吴镇">鄣吴镇</option>
          <option value="高禹镇">高禹镇</option>
          <option value="梅溪镇">梅溪镇</option>
          <option value="溪龙乡">溪龙乡</option>
          <option value="昆铜乡">昆铜乡</option>
          <option value="章村镇">章村镇</option>
          <option value="报福镇">报福镇</option>
          <option value="上野乡">上野乡</option>
          <option value="天荒坪镇">天荒坪镇</option>
          <option value="山川乡">山川乡</option>
        </select>
      </p>
      <!-- 封面 -->
      <div class="formtr formtrDiv clear">
        <span>封面：</span>
        <div class="xc_cover">请选择封面图片</div>
        <span>&nbsp;</span>
        <a href="" class="a-file-btn-input">
          <w>请选择图片</w>
          <input type="file" class="file-btn-input" id="" @change="setImagePreviews1('file-btn-input','xc_cover',0)">
        </a>
      </div>
      <!-- 选项	 -->
      <div class="choose_type">
        <ul>
          <li :class="{ activeXC: showStr === 'techan' }" @click="showTab('techan')">特产</li>
          <li :class="{ activeXC: showStr === 'meijing' }" @click="showTab('meijing')">美景</li>
          <li :class="{ activeXC: showStr === 'wenhua' }" @click="showTab('wenhua')">文化</li>
          <li :class="{ activeXC: showStr === 'huodong' }" @click="showTab('huodong')">活动</li>
        </ul>
      </div>
      <!-- 特产 -->
      <div class="special clear" v-if=" showStr === 'techan' ">
        <ul>
          <li class="special_ed"  v-for="(oneTC, index) in tcList" :key="index">
            <div class="show_spe">
              <img :src="oneTC.imgSrc">
            </div>
            <input type="text" size="10" class="input_spe" v-model="oneTC.tcName">
            <div>
              <a href="" class="a-file-btn-input">
                <w>请选择图片</w>
                <input type="file" class="file-btn-input cho_spe" @change="setImagePreviews2('cho_spe','show_spe',index)">
              </a>
              <input type="button" class="delete_spe" value="取消" @click="removeTC(index)"/>
            </div>
          </li>
          <li class="special_ing">
            <img class="add_tc" src="../../icons/add.png" @click="addNewTC" />
          </li>
        </ul>
      </div>
      <!-- 美景 -->
      <div class="sight clear" v-if=" showStr === 'meijing' ">
        <ul>
          <li class="sight_pic clear">
            <div class="sight_pic_btn">
              <a href="" class="a-file-btn-input btn-grayd">
                <w>上传图片集</w>
                <input type="file" class="file-btn-input files-input-btn" multiple="multiple" @change="setImagePreviews3">
              </a>
            </div>
            <div class="sight_pic_div files-show clear">
              <div v-for="(mjs, index) in imageList">
                <div class="img-cell" v-for="(mj, index2) in mjs">
                  <img :src="mj" alt="">
                  <button class='btn btn-sm btn-gray btn-delfun' type='button'>删除</button>
                </div>
                <div style="width: 98%;height: 0px;border: 1px solid lightgray;clear: both;margin:10px auto 15px" v-show="imageList.length >= 1"></div>
              </div>
            </div>
          </li>
          <li class="sight_pic clear">
            <div class="sight_pic_btn">
              <a href="" class="a-file-btn-input btn-grayd">
                <w>上传视频</w>
                <input type="file" class="file-btn-input file-video-btn" multiple="multiple" @change="setImagePreviews4">
              </a>
            </div>
            <div class="sight_pic_div file-video clear">
              <div class="video-cell" v-for="(video, index) in videoList">
                <video controls>
                  <source :src="video" type="video/mp4">
                </video>
                <button class='btn btn-sm btn-gray btn-delfun-video' type='button'>删除</button>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <!-- 文化 -->
      <div class="editor clear"  v-if=" showStr === 'wenhua' ">
        <vue-html5-editor @change="updateData1" :content="wenhua" :height="500" :z-index="1000" :auto-height="true" ></vue-html5-editor>
      </div>
      <!-- 活动 -->
      <div class="editor clear"  v-if=" showStr === 'huodong' ">
        <vue-html5-editor @change="updateData2" :content="huodong" :height="500" :z-index="1000" :auto-height="true" ></vue-html5-editor>
      </div>
      <p class="btnbar">
        <span>　　　</span>
        <input type="submit" value="发表" class="btn" @click="sendXiangcun">
        <router-link :to="{path: '/show/xiangcunShow2', query: { name: xcName, introduction: xcIntroduction, lishuxian: lishuxian, fengmian: xcCover, techang: tcList, images: imageList, videos: videoList, wenhua: wenhua,huodong: huodong}}" class="btn" target="_blank">
          预览
        </router-link>
        <input type="reset" value="重置" class="btn btn-gray">
      </p>
    </form>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        showStr: 'techan',
        wenhua: 'wenhua',
        huodong: '活动',
        xcName: '',
        xcIntroduction: '',
        xcCover: '',
        lishuxian: '递铺镇',
        TClength: 0,
        /* 特产的单位元素 */
        newTC: {
          imgSrc: '',
          tcName: ''
        },
        /* 特产集 是json格式的 imgSrc，tcName */
        tcList: [],
        /* 图片集 */
        imageList: [],
        /* 视频集 */
        videoList: [],
        /* 图片集二维数组的索引 [PicIndex][] */
        PicIndex: 0,
        /* 图片集二维数组的 单位元素的数组 临时存放 */
        linshiPic: [],
        /* 后台乡村 */
        dataXiangcun: {
          name: '',
          introduction: '',
          lishuxian: '',
          fengmian: '',
          techang: [],
          images: [],
          videos: [],
          wenhua: '',
          huodong: '',
          author: ''
        },
        paramXc: new window.FormData()
      }
    },
    created () {
      this.menun()
    },
    methods: {
      showTab (str) {
        this.showStr = str
        if (str === 'techan') {
          if (this.tcList.length === 1 && this.tcList[0].imgSrc === '') {
            this.TClength = 0
          } else {
            this.TClength = this.tcList.length
          }
          console.log('当前的值为TClength：' + this.TClength)
        }
      },
      updateData1 (data) {
        this.wenhua = data
        console.log(data)
      },
      updateData2 (data) {
        this.huodong = data
        console.log(data)
      },
      menun () {
        window.scrollTo(0, 0)
      },
      // 封面图片添加函数
      setImagePreviews1 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.paramXc.append('fengmian', choosePic.files[0], choosePic.files[0].name)
        }
      },
      // 特产图片添加函数
      setImagePreviews2 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        // window.alert(fileList.length)
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' title='" + index + "' />"
          // 使用:src='" + imgsrc + "'时，无法显示图片，直接使用下面的方式图片就可显示，但结果路径都是一样的
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index - this.TClength]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.tcList[index].imgSrc = imgObjPreview.src
        }
        this.paramXc.append('techang/pic' + (this.tcList.length - 1), choosePic.files[0], choosePic.files[0].name)
      },
      // 特产图片外框添加函数
      addNewTC () {
        this.tcList.push(JSON.parse(JSON.stringify(this.newTC)))
      },
      removeTC (index) {
        this.tcList.splice(index, 1)
      },
      /* 发送乡村的数据 */
      sendXiangcun () {
        this.dataXiangcun.fengmian = this.xcCover
        this.dataXiangcun.techang = this.tcList
        this.dataXiangcun.images = this.imageList
        this.dataXiangcun.videos = this.videoList
        this.paramXc.append('name', this.xcName)
        this.paramXc.append('introduction', this.xcIntroduction)
        this.paramXc.append('lishuxian', '浙江省湖州市安吉县' + this.lishuxian)
        this.paramXc.append('images', this.imageList)
        this.paramXc.append('videos', this.videoList)
        this.paramXc.append('wenhua', this.wenhua)
        this.paramXc.append('huodong', this.huodong)
        for (var i = 0; i < this.tcList.length; i++) {
          this.paramXc.append('techang/name' + i, this.tcList[i].tcName)
        }
        console.log('发送的消息是：{ 标题：' + this.dataXiangcun.name + ',简介： ' + this.dataXiangcun.introduction + ',隶属镇： ' + this.dataXiangcun.lishuxian + ',封面： ' + this.dataXiangcun.fengmian + ',特产： ' + this.dataXiangcun.techang + ',图片集： ' + this.dataXiangcun.images + ',视频： ' + this.dataXiangcun.videos + ',文化： ' + this.dataXiangcun.wenhua + ',活动： ' + this.dataXiangcun.huodong + ' }')
      },
      // 照片集添加需改正
      setImagePreviews3 () {
        var choosePic = document.getElementsByClassName('files-input-btn')[0]
        var showPic = document.getElementsByClassName('files-show')[0]
        var cLength = choosePic.files.length
        var iLength = this.imageList.length
        if (cLength >= 1) {
          this.linshiPic = []
        }
        /* window.alert('选择照片的张数：  ' + cLength + '\n图片集的长度为： ' + iLength) */
        for (var j = 0; j < cLength;) {
          /*  window.alert('j一一一一： ' + j + '\n图片集长度  ： ' + this.imageList.length + '\n当前序号：   ' + (iLength + j)) */
          if (/^((?!image).)+/g.test(choosePic.files[j].type)) {
            window.alert('请选择正确的图片格式！')
            j = 99999
          } else {
            showPic.innerHTML += "<div class='img-cell" + (j + iLength) + "' style='width: 190px;height: 240px;float:left;margin: 0px 5px 5px 0px'>" + "<img class='pic_coll" + (j + iLength) + "' src='" + window.URL.createObjectURL(choosePic.files[j]) + "' style='width:190px;height:190px;display: block'><button class='btn btn-sm btn-gray btn-delfun' style='height: 30px;margin-top: 10px' type='button'>删除</button>"
            this.linshiPic[j] = window.URL.createObjectURL(choosePic.files[j])
            j = j + 1
          }
        }
        if (cLength >= 1) {
          this.imageList[this.PicIndex] = this.linshiPic
          this.PicIndex = this.PicIndex + 1
        }
        console.log('当前数组的长度' + this.PicIndex + '/n选择照片的张数' + cLength)
        showPic.innerHTML += '<div style="width: 98%;height: 0px;border: 1px solid lightgray;clear: both;margin:10px auto 15px"></div>'
      },
      setImagePreviews4 () {
        var choosePic = document.getElementsByClassName('file-video-btn')[0]
        var showPic = document.getElementsByClassName('file-video')[0]
        var cLength = choosePic.files.length
        var iLength = this.videoList.length
        /* window.alert('选择照片的张数：  ' + cLength + '\n图片集的长度为： ' + iLength) */
        for (var j = 0; j < cLength; j++) {
          /*  window.alert('j一一一一： ' + j + '\n图片集长度  ： ' + this.imageList.length + '\n当前序号：   ' + (iLength + j)) */
          if (/^((?!video).)+/g.test(choosePic.files[j].type)) {
            window.alert('请选择正确的视频格式！')
            j = 99999
          } else {
            showPic.innerHTML += "<div style='width: 300px;height: 250px;float: left;margin-right: 5px;margin-bottom: 5px;'><video style='display:block;width:100%;height:100%;' controls><source class='video" + (j + iLength) + "' src='" + window.URL.createObjectURL(choosePic.files[j]) + "' type='video/mp4'></video><button class='btn btn-sm btn-gray btn-delfun-video' type='button' style='height: 30px;margin-top: 10px;'>删除</button></div>"
            this.videoList[iLength + j] = window.URL.createObjectURL(choosePic.files[j])
          }
        }
      }
    }
  }
</script>


<style scoped>
  @import "../../styles/style.css";
  .editor{
    width: 90%;
    margin: 0 auto;
  }
  .specialCell img{
    width:80%;
    margin-right: 0 auto;
  }
  .activeXC{
    color:#FFDE3A;
    border-top:3px solid #FFDE3A;
  }
  .main{
    /*height: 800px;*/
    width: 1000px;
    margin: 0px auto;
    /*background: rgba(55,220,188,0.7);*/
    /*box-shadow: 0px -4px 3px -3px #FFDE3A;*/
  }
  .hengg1{
    height: 2px;
    /* width: 100%;
    margin: 0px auto 40px; */
    width: 1200px;
    margin: 0px -100px 40px;
    background: #ccc;
  }
  /*标题*/
  .add_title{
    width: 100%;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 24px;
    letter-spacing: 10px;
  }

/*选项卡*/
  .choose_type{
    height: 65px;
    width: 100%;
  }
  .choose_type ul{
    border-top: 2px solid #ccc;
    width: 100%;
  }
  .choose_type ul li{
    height: 65px;
    line-height: 50px;
    margin-left: 30px;
    float: left;
    cursor: pointer;
    margin-top: -2px;
    padding: 0 5px;
  }
/*特产*/
  .add_tc{
    margin: 125px 88px;
    cursor: pointer;
  }
  .special{
    width: 100%;
  }
  .special ul{
    width: 98%;
    margin-left: 2%;
  }
  .special_ed{
    width: 23%;
    margin: 0px 2% 20px 0px;
    height: 300px;
    float: left;
  }
  .special_ing{
    width: 22.5%;
    margin: 0px 2% 20px 0px;
    height: 300px;
    float: left;
  }
  .show_spe{
    width: 100%;
    height: 200px;
    background: #f2f2f2;
    border-radius: 3px;
  }
  .show_spe img{
    width: 100%;
    height:200px;
  }
  .input_spe{
    height: 30px;
    width: 100%;
    margin: 15px 0px;
    border: 1px solid #ccc;
    border-radius: 3px;
    box-sizing: border-box;
  }
  .special_ing{
    border: 2px solid #f2f2f2;
    border-radius: 4px;
  }
  .delete_spe{
    padding: 5px 10px;
    background: #F2F2F2;
    border-radius: 3px;
    margin-left: 65px;
    border: 2px solid #F2F2F2;
    width: 70px;
    font-size: small;
    height: 32px;
    font-family: '微软雅黑';
    cursor: pointer;
  }
/*美景*/
  .sight{
    width: 100%;
  }
  .sight ul{
    width: 98%;
    margin-left: 2%;
  }
  .sight_pic .sight_pic_div{
    width: 100%;
    min-height: 320px;
    margin: 5px auto;
    border: 2px solid #f2f2f2;
    border-radius: 4px;
    padding: 10px 0px 0px 10px;
  }
  .sight_pic,.sight_vid{
    width: 100%;
    margin: 0px 2% 20px 0px;
    float: left;
  }
  .sight_pic>button,.sight_vid>button{
    width: 43%;
    margin: 129px 30%;
    height: 40px;
    line-height: 34px;
    text-align: center;
    background: #f2f2f2;
    border-radius: 3px;
    border: 1px solid #f2f2f2;
  }
  .sight_pic_btn{
    width: 100%;
    height:32px;
  }
/* 图片集 */
  .img-cell{
    width: 190px;
    height: 240px;
    float:left;
    margin: 0px 5px 5px 0px;
  }
  .img-cell img{
    width: 190px;
    height: 190px;
  }
  .btn-delfun{
    height: 30px;
    margin-top: 10px;
  }
  .btn-delfun-video{
    height: 30px;
    margin-top: 10px;
  }
  .video-cell{
    width: 300px;height: 250px;float: left;margin-right: 5px;margin-bottom: 5px;
  }
  .video-cell video{
    display:block;width:100%;height:100%;
  }
</style>



