        <span>&nbsp;</span>
        <a href="" class="a-file-btn-input">
          <w>请选择图片</w>
          <input type="file" class="file-btn-input" id="" @change="setImagePreviews1('file-btn-input','add_cover',0)" >
        </a>
      </div>
