          <div class="div-file-btn-input">
            <a href="" class="a-file-btn-input">
              <w>请选择图片</w>
              <input type="file" class="file-btn-input files-input" id="" multiple="multiple" @change="setImagePreviews2()" >
            </a>
          </div>
