<template>
  <div class="contain">
		<div class="hg1"></div>
		<form>
			<ul>
				<li class="input_til">邮&emsp;&emsp;箱：</li>
				<li><input type="text" v-model="user.email" class="input_con input_conl"></li>
			</ul>
			<ul>
				<li class="input_til">密&emsp;&emsp;码：</li>
				<li><input type="password" v-model="user.password" class="input_con input_conl"></li>
				<li class="ma_notice"></li>
			</ul>
			<ul>
				<li class="input_til">验&ensp;证&ensp;码：</li>
				<li><input type="text" class="input_con input_cons" v-model="user.checkcode"></li>
				<li class="ma_con"><img alt="验证码" id="" :src="yanzhensrc"/></li>
				<li class="ma_notice"><span @click="reloadCode">看不清，换一张</span></li>
			</ul>
			<ul>
				<li class="input_til">类&emsp;&emsp;型：</li>
				<li><label class="ut_choose" @click="changeType(0)"><input name="userType" type="radio" value="1" checked="true"/>&ensp;普通用户</label></li>
				<li><label class="ut_choose" @click="changeType(1)"><input name="userType" type="radio" value="2" />&ensp;商业用户</label></li> 
			</ul>
			<ul>
				<li><input type="reset" class="res" value="重置"></li>
				<li><input type="submit"  class="sub" value="提交" @click.prevent="postData(user)"></li>
			</ul>
		</form>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        user: {
          email: '',
          password: '',
          checkcode: '',
          type: '0'
        },
        yanzhensrc: 'http://192.168.5.112:8000/ssm01/servlet/ImageServlet'
      }
    },
    methods: {
      postData (data) {
        axios.post('ssm01/user/register', data, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
          }
        })
          .then(function (data) {
            console.log(data)
            console.log(data.data)
            if (data.data.errno === 0) {
              console.log('上传成功')
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      reloadCode () {
        var time = new Date().getTime()
        this.yanzhensrc = 'http://192.168.5.112:8000/ssm01/servlet/ImageServlet?d=' + time
      },
      changeType (str) {
        this.user.type = str
        console.log(str)
      }
    }
  }
</script>
<style scoped>
@import '../../styles/main.css';
.hg1{
	width: 36px;
	height: 3px;
	background: #FFDE3A;
	margin: -2px 20px;
}
.contain{
	width: 94%;
	/*height: 320px;*/
	margin: 0px 3%; 
	/*background: rgba(111,222,55,0.8);*/
}
form{
	width: 800px;
	height: 300px;
	margin: 20px auto 0px;
	/*background: rgba(111,122,55,0.8);*/
}
form ul{
	width: 600px;
	height: 50px;
	margin:8px 0px 8px 200px;
	/*background: red;*/
}
form ul li{
	float: left;
}
.input_til{
	color: #fff;
	height: 50px;
	line-height: 50px;
	margin-right: 15px;
}
.input_con{
	border: 1px solid rgba(140,140,140,0.2);
	border-radius: 5px;
	background: rgba(140,140,140,0.4);
	height: 34px;
	color: #fff;
	margin-top: 6px;
	line-height: 34px;
	padding-left: 5px;
}
.input_conl{
	width: 300px;
}
.input_cons{
	width: 100px;
}
.ma_con{
	width: 55px;
  height: 34px;
  background: #ccc;
  margin: 8px 0px 8px 15px;
}
.ma_con img{
  width: 100%;
  height: 100%;
}
.ma_notice{
	height: 50px;
	line-height: 50px;
	margin-left: 10px;
	font-size: 12px;
}
.ma_notice span{
	color: rgba(165,165,165,0.9);
	cursor: pointer;
}
.res,.sub{
	width: 70px;
	height: 30px;
	font-size: 12px;
	background: #FFDE3A;
	border-radius: 3px;
	border: 1px solid #FFDE3A;
	font-family: '微软雅黑';
	cursor: pointer;
	margin-top: 20px;
}
.res{
	margin: 20px 30px 0px 230px;
}
textarea{
	font-family: '微软雅黑';
	font-size: 16px;
}
.ut_choose{
	height: 50px;
	line-height: 50px;
	margin-right: 30px;
	color: #fff;
	cursor: pointer;
}
</style>



