<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span class="active"><a href="##">我的乡村</a></span>
    </div>
    <div class="midr2 content">
      <div>
        <router-link class="btn btn1" :to="{path: '/add/xiangcunAdd'}" target="_blank">
          添加乡村
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div class="cell" v-for="(myXc, index) in myCountry">
        <img :src="myXc.xcImg">
        <div class="cellr">
          <router-link :to="{path: '/show/countryside', query: { id: myXc.xcId }}" target="_blank">
            <h4 v-text="myXc.xcName"></h4>
          </router-link>
          <p class="time" v-text="myXc.xcTime"></p>
          <p>
            <router-link class="btn btn-sm btn-gray" :to="{path: '/change/xiangcunChange', query: { id: myXc.xcId }}" target="_blank">
              修改
            </router-link>
          </p>
        </div>
      </div>
      <div style="clear:both"></div>
    </div>
    <div style="clear:both"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        myCountry: [
          {
            xcId: '1',
            xcName: '高家堂村村村村村',
            xcTime: '2017/3/7',
            xcImg: 'static/special1.jpg'
          },
          {
            xcId: '2',
            xcName: 'nihao',
            xcTime: '2017/3/7',
            xcImg: 'static/special2.jpg'
          }
        ]
      }
    },
    created () {
      this.getData()
    },
    methods: {
      getData () {
        axios.get('user/xiangcun1').then(function (res) {
          this.myCountry = res.data
        }).catch(function (err) {
          console.log(err)
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
</style>



