      showVidIndex: 0,
      showActIndex: 0,
      actInfo: [
        { title: '活动一', content: '<p>活动和动作都是以实现预定目的为特征的，但是动作受单一目的的制约。而活动则受一种完整的目的和动机系统的制约。活动是由一系列动作构成的系统。</p>', time: '2017-8-18' },
        { title: '活动二', content: '<p>活动和动作都是以实现预定目的为特征的，但是动作受单一目的的制约。而活动则受一种完整的目的和动机系统的制约。活动是由一系列动作构成的系统。</p>', time: '2017-8-18' }
      ],
