        { src: 'static/1.1.2.mp4', title: '安吉白茶1' },
        { src: 'static/1.1.2.mp4', title: '安吉白茶2' }
      ],
      shouedShow: false,
      shouTShow: false
    }
  },
  computed: {
    nowImg () {
      return this.techanInfo[this.nowImgNum].src   /* 主图的索引号动态改变 */
    },
    nowText () {
      return this.techanInfo[this.nowImgNum].name    /* 主图下文字的索引号动态改变 */
    },
    move () {
      return {
        transform: 'translateY(-' + this.nowLocation + 'px)',
        color: 'red',
        transition: 'all 0.5s'
      }
    },
    nowAct () {
      return this.actInfo[this.showActIndex]
    },
    nowVid () {
      return this.videoInfo[this.showVidIndex]
    }
  },
  methods: {
    ChangeSightShow (str) {
      this.sight_show = str
    },
    ChangeSpecialShow (str) {
      this.special_show = str
    },
    ChangePlayShow (str) {
      this.play_show = str
    },
    showStar (num) {
      this.starNum = num
    },
    showShowed (str) {
      this.shouedShow = str
    },
    addComment () {
      this.comment.push({
        name: '我',
        headp: 'static/hp1.jpg',
        time: getTime(),
        content: this.commentText,
        like_show: 'like',
        dislike_show: 'dislike',
        like: 0,
        dislike: 0,
        scores: this.starNum
      })
      this.commentText = ''
      this.starNum = 0
    },
    ChangeLike (item, str, n) {
      item.like_show = str
      item.like += n
    },
    ChangeDislike (item, str, n) {
      item.dislike_show = str
      item.dislike += n
    },
    changeImgIndex (index) {
      /* window.alert(index) */
      this.nowImgNum = index
    },
    /* 图片的高度与跳动距离有关 */
    btnDown () {
      /* window.alert(1) */
      /* this.style = 'move' */
      /* 用 :class="style" 改变style的值 */
      /* window.alert(this.aaa.length) */
      if (this.nowLocation === 121 * (this.techanInfo.length - 3)) {
        this.nowLocation = 0
      } else {
        this.nowLocation += 121
      }
      this.style2 = this.move
    },
    btnUp () {
      if (this.nowLocation === 0) {
        this.nowLocation = 121 * (this.techanInfo.length - 3)
      } else {
        this.nowLocation -= 121
      }
      this.style2 = this.move
    },
    showChange (obj, index) {
      if (obj === 'modal-img') {
        this.showModalValue = 'modal-img'
        this.imgInfo = this.imgsInfo[index]
      } else if (obj === 'modal-text') {
        this.showModalValue = 'modal-text'
        this.showActIndex = index
      } else if (obj === 'modal-video') {
        this.showModalValue = 'modal-video'
        this.showVidIndex = index
      } else {
        this.showModalValue = ''
      }
    },
    showBtn () {
      this.showButtonValue = !this.showButtonValue
    },
    showBefore () {
      if (this.showImgIndex === 0) {
        this.showImgIndex = this.imgInfo.length - 1
      } else {
        this.showImgIndex -= 1
      }
    },
    showAfter () {
      if (this.showImgIndex === this.imgInfo.length - 1) {
        this.showImgIndex = 0
      } else {
        this.showImgIndex += 1
      }
    },
    showTShow (str) {
      this.shouTShow = str
    },
    getContryside () {
      const self = this
      axios.get('countryside').then(function (res) {
        console.log(res.data[0])
        self.cCover = res.data[0].mainpic
      }).catch(function (err) {
        console.log('获取乡村信息失败' + err)
      })
    }
  }
}
function getTime () {
  var now = new Date()
  var year = now.getFullYear()
  var month = now.getMonth() + 1
  var day = now.getDate()
  month.length < 2 ? '0' + month : month
  day.length < 2 ? '0' + day : day
  return year + '-' + month + '-' + day
}
</script>
