<template>
  <div>
    <div class="headbar">
    </div>
    <div class="bodybar">
      <div class="top">
        <img :src="yj.yjImg">
        <div class="overwrap"></div>
        <div class="p">
          <div class="title">·<span v-text="yj.yjName"></span></div>
          <div class="author" v-text="yj.yjAuthor"></div>
          <div class="time" v-text="yj.yjTime"></div>
        </div>
        <div class="next-btn">
          <a href=""><img src="../../images/down.png" alt=""></a>
        </div>
      </div>
      <div contenteditable="false" class="content" v-html="yj.yjCon">
      </div>
      <div class="like">
        <a href="javascript:;" @click=" showa ('likeA',1) " v-if=" show === 'like' ">
          <img src="../../images/like.png">
        </a>
        <a href="javascript:;" @click=" showa ('like',-1) " v-if=" show === 'likeA' ">
          <img src="../../images/likeA.png">
        </a>
        <span v-text="yj.yjLikeNum"></span>
      </div>
    </div>
    <div class="footbar">
    </div>
  </div>
</template>

