        },
        shuju: ''
      }
    },
    created () {
      this.menun()
      this.shuju = this.GetQueryString('id')
      this.yj.yjAuthor = decodeURI(this.GetQueryString('author'))
      this.yj.yjCon = decodeURI(this.GetQueryString('content'))
      this.yj.yjid = this.GetQueryString('id')
      this.yj.yjImg = decodeURI(this.GetQueryString('mainpic').replace(/%3A/g, ':').replace(/%2F/g, '/'))
      this.yj.yjName = decodeURI(this.GetQueryString('name'))
      this.yj.yjTime = this.GetQueryString('articleTime')
      this.yj.yjLikeNum = 2222
      this.getData()
    },
    methods: {
      showa (str, num) {
        this.show = str
        this.yj.yjLikeNum += num
      },
      menun () {
        window.scrollTo(0, 0)
        /* window.alert(window.location.href) */
      },
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      }
    }
  }
</script>

<style scoped>
  @import '../../styles/style.css';
  .top{
    width:100%;
    height:590px;
    position: relative;
  }
  .top>img{
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .top .overwrap{
    width:100%;
    height:100%;
    position: absolute;
    background: rgba(48,48,48,0.45);
  }
  .top .p{
    min-width: 40%;
    height: 50%;
    right: 0px;
    top:50%;
    transform: translateY(-50%);
    position: absolute;
    padding-right: 150px;
    text-align: right;
  }
  .top .p .title{
    font-size: 52px;
    font-weight: bold;
    color: #ffffff;
    height:60%;
    line-height: 4;
  }
  .top .p .author{
    color: #ffffff;
    font-weight: bold;
    font-size: 22px;
    height: 20%;
  }
  .top .p .time{
    color: #aaa;
    height:20%;
    font-size: 22px;
  }
  .top .next-btn{
    width: 100%;
    height: auto;
    position: absolute;
    text-align: center;
    bottom: 0;
  }
  .top .next-btn img{
    width: 110px;
    height: 60px;
  }
  .content{
    width: 1100px;
    height: auto;
    margin:0 auto;
    min-height: 550px;
    padding: 50px 100px;
    box-sizing: border-box;
  }
  .content img{
    width: 80%;
    height: auto;
    margin: 20px 10%;
  }
  .content p{
    line-height: 2;
  }
  .like{
    width: 80px;
    height:40px;
    line-height: 40px;
    margin:50px auto;
    text-align: center;
  }
  .like span{
    height:40px;
    line-height: 40px;
    width: 40px;
    display: block;
    float: left;
  }
  .like a img{
    height:40px;
    width: 40px;
    line-height: 40px;
  }
  .like a{
    width: 40px;
    height: 40px;
    display: block;
    float: left;
  }
</style>



