<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span :class="{active: show === 'tab1'}"><a href="javascript:;" @click="showTab('tab1')">修改个人信息</a></span>
      <span :class="{active: show === 'tab2'}"><a href="javascript:;" @click="showTab('tab2')">修改密码</a></span>
    </div>
    <div class="midr3 content" v-if="show === 'tab1'">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span>昵称：</span>
          <input type="text" class="text-input" v-model="userName">
        </p>
        <div class="formtr formtrDiv clear">
          <span>头像：</span>
          <div class="xc_cover2">
            <img :src="tx" alt="">
          </div>
          <span>&nbsp;</span>
          <a href="" class="a-file-btn-input">
            <w>请选择图片</w>
            <input type="file" class="file-btn-input" id="" multiple="multiple" @change="setImagePreviews1('file-btn-input','xc_cover2',0)">
          </a>
        </div>
        <p class="formtr formtrp">
          <span>性别：</span>
          <input type="radio" value="0" name="sex" :checked="sex === 0" @click="sex=0">
          <lable class="input_radio" for="sex">男</lable>
          <input type="radio" value="1" name="sex" :checked="sex === 1" @click="sex=1">
          <lable class="input_radio" for="sex">女</lable>

        </p>
        <p class="formtr formtrp">
          <span>生日：</span>
          <input type="text" class="text-input" placeholder="2017-01-01" v-model="birthday">
        </p>
        <p class="formtr formtrp">
          <span>手机：</span>
          <input type="text" class="text-input" v-model="phone">
        </p>
        <p class="formtr formtrp">
          <span>微信：</span>
          <input type="text" class="text-input" v-model="wechat">
        </p>
        <p class="formtr formtrp">
          <span>居住城市：</span>
          <input type="text" class="text-input" placeholder="上海"  v-model="city">
        </p>
        <p class="formtr formtrp">
          <span>个性签名：</span>
          <input type="text" class="text-input" v-model="signature">
        </p>
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="保存" class="btn" @click="sendData(userInfo2)">
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <div class="midr2 content" v-if="show === 'tab2'">
      <form action="##" class="form">
        <p class="formtr formtrp">
          <span>旧密码：</span>
          <input type="password" class="text-input" v-model="passwd1">
        </p>
        <p class="formtr formtrp">
          <span>新密码：</span>
          <input type="password" class="text-input" v-model="passwd2">
        </p>
        <p class="formtr formtrp">
          <span>再次输入：</span>
          <input type="password" class="text-input" v-model="passwd3">
        </p>
        <p class="btnbar">
          <span>　　　</span>
          <input type="button" value="保存" class="btn" @click="postData">
          <input type="reset" value="重置" class="btn btn-gray">
        </p>
      </form>
    </div>
    <div style="clear:both"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        show: 'tab1',
        userName: this.userInfo.userName,
        sex: this.userInfo.sex,
        birthday: this.userInfo.birthday,
        city: this.userInfo.city,
        signature: this.userInfo.signature,
        phone: this.userInfo.phone,
        wechat: this.userInfo.wechat,
        tx: this.userInfo.userPic,
        passwd1: '',
        passwd2: '',
        passwd3: '',
        userInfo2: {
          username: '',
          sex: '',
          birth: '',
          city: '',
          signature: '',
          headPIC: '',
          phone: '',
          wechat: ''
        },
        password: {
          oldpwd: '',
          newpwd: ''
        }
      }
    },
    methods: {
      showTab (str) {
        this.show = str
      },
      postData () {
        if (this.passwd2 !== this.passwd3) {
          window.alert('两次输入的密码不相同！')
        } else if (!/^([a-zA-Z0-9]|[_]){6,20}$/.test(this.passwd2)) {
          window.alert('密码长度6-20位，且只能包含符号‘_’')
        } else {
          this.password.oldpwd = this.passwd1
          this.password.newpwd = this.passwd2
          axios.post('http://127.0.0.1:8000/ssm01/user/changepwd', this.password, {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            }
          }).then(function (res) {
            if (res.data === '修改密码成功') {
              window.alert('修改密码成功')
            } else if (res.data === '旧密码不正确') {
              window.alert('旧密码不正确')
            } else {
              window.alert('修改密码失败')
            }
          }).catch(function (err) {
            console.log(err)
          })
        }
      },
      sendData (userInfo2) {
        this.userInfo2.username = this.userName
        this.userInfo2.sex = this.sex
        this.userInfo2.birth = this.birthday
        this.userInfo2.city = this.city
        this.userInfo2.signature = this.signature
        this.userInfo2.headpic = this.tx
        this.userInfo2.phone = this.phone
        this.userInfo2.wechat = this.wechat
        console.log('要上传的数据：{用户名：' + this.userInfo2.username + ',性别： ' + this.userInfo2.sex + ',生日： ' + this.userInfo2.birth + ',城市： ' + this.userInfo2.city + ',个性签名： ' + this.userInfo2.signature + ',头像： ' + this.userInfo2.headpic + ',手机： ' + this.userInfo2.phone + ',微信： ' + this.userInfo2.wechat)
        axios.get('http://127.0.0.1:8000/ssm01/user/changeUserInfo', userInfo2).then(function (res) {
          if (res.data === '修改成功') {
            window.alert('修改成功~')
          } else if (res.data === '用户名已存在') {
            window.alert('用户名已存在~')
          } else if (res.data === '手机号已注册') {
            window.alert('手机号已注册~')
          } else {
            window.alert('修改失败~')
          }
        }).catch(function (err) {
          console.log(err)
        })
      },
      setImagePreviews1 (class1, class2, index) {
        var choosePic = document.getElementsByClassName(class1)[index]
        var showPic = document.getElementsByClassName(class2)[index]
        showPic.innerHTML = ''
        var fileList = choosePic.files
        for (var i = 0; i < fileList.length; i++) {
          showPic.innerHTML += "<img class='img" + class2 + i + "' style='display:block;width:100%;height:100%;' />"
          var imgObjPreview = document.getElementsByClassName('img' + class2 + i)[index]
          imgObjPreview.src = window.URL.createObjectURL(choosePic.files[i])
          this.tx = imgObjPreview.src
        }
      }
    },
    props: ['userInfo'],
    created () {
      this.getData()
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
</style>



