<template>
  <div class="midr">
    <div class="midr1 mid1">
      <span class="active"><a href="##">我的景点</a></span>
    </div>
    <div class="midr2 content">
      <div>
        <router-link class="btn btn1" :to="{path: '/add/jingdianAdd'}" target="_blank">
          添加景点
        </router-link>
        <div style="clear:both"></div>
      </div>
      <div class="cell" v-for="(myJd, index) in myJingdian">
        <img :src="myJd.jdImg">
        <div class="cellr">
          <router-link :to="{path: '/displayremark/sight', query: { id: myJd.jdId }}" target="_blank">
            <h4 v-text="myJd.jdName"></h4>
          </router-link>
          <p class="time" v-text="myJd.jdTime"></p>
          <p>
            <router-link class="btn btn-sm btn-gray" :to="{path: '/add/jingdianAdd2', query: { id: myJd.jdId }}" target="_blank">
              修改
            </router-link>
          </p>
        </div>
      </div>
      <div style="clear:both"></div>
    </div>
    <div style="clear:both"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        myJingdian: [
          {
            jdName: '大峡谷大峡谷大峡谷大峡谷大峡谷大峡谷大峡谷大峡谷大峡谷',
            jdImg: 'static/special1.jpg',
            jdId: 1,
            jdTime: '2017/3/7'
          },
          {
            jdName: '峡谷大峡谷大峡谷大峡谷大峡谷峡谷大峡谷大峡谷',
            jdImg: 'static/special2.jpg',
            jdId: 2,
            jdTime: '2017/3/7'
          }
        ]
      }
    },
    created () {
      this.getData()
    },
    methods: {
      getData () {
        axios.get('user/jingdian1').then(function (res) {
          this.myJingdian = res.data
        }).catch(function (err) {
          console.log(err)
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import "../../styles/mid.css";
</style>



